
.panel-image {
    background-repeat: no-repeat;
    background-size: cover!important;
    -moz-background-size: cover!important;
    -webkit-background-size: cover!important;
    -o-background-size: cover!important;
    background-position: 50%;
    overflow: hidden;
    background-position: center;
}

.bg-primary {
	background-color: get-color(primary);
}
.bg-secondary {
	background-color: get-color(secondary);
}
.bg-soft-secondary {
	background-color: #edf2fc !important;
}
.wrapper.bg-soft-secondary.angled.lower-start:after,
.wrapper.bg-soft-secondary.angled.upper-start:before {
	border-left-color: #edf2fc;
}
.wrapper.bg-soft-secondary.angled.lower-end:after,
.wrapper.bg-soft-secondary.angled.upper-end:before {
	border-right-color: #edf2fc;
}
.bg-success {
	background-color: get-color(success);
}
.bg-warning {
	background-color: get-color(warning);
}
.bg-alert {
	background-color: get-color(alert);
}
.bg-light-grey {
	background-color: get-color(light-grey);
}
.bg-medium-grey {
	background-color: get-color(medium-grey);
}
.bg-dark-grey {
	background-color: get-color(dark-grey);
}
.bg-white {
	background-color: get-color(white-dragon);
}
.bg-pale-red {
	background-color: #fae6e7 !important;
}
.bg-pale-green {
	background-color: #e1f6f0 !important;
}
.image-wrapper:not(.mobile) {
	background-attachment: fixed !important;
}
.image-wrapper.bg-auto {
	background-size: auto;
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: scroll !important;
}
.image-wrapper.bg-full {
	background-size: 100%;
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: scroll !important;
}
.image-wrapper.bg-cover {
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: scroll !important;
}
.image-wrapper.bg-overlay:before {
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	width: 100%;
	height: 100%;
	background: rgba(30, 34, 40, 0.5);
}
.image-wrapper.bg-overlay-300:before {
	background: rgba(30, 34, 40, 0.3);
}
.image-wrapper.bg-overlay-400:before {
	background: rgba(30, 34, 40, 0.4);
}
.image-wrapper.bg-overlay-light-300:before {
	background: rgba(255, 255, 255, 0.3);
}
.image-wrapper.bg-overlay-light-400:before {
	background: rgba(255, 255, 255, 0.4);
}
.image-wrapper.bg-overlay-light-500:before {
	background: rgba(255, 255, 255, 0.5);
}
.image-wrapper.bg-overlay-light-600:before {
	background: rgba(255, 255, 255, 0.6);
}
.image-wrapper.bg-overlay:not(.bg-content) *,
.image-wrapper.bg-overlay.bg-content .content {
	position: relative;
	z-index: 2;
}
.image-wrapper.bg-overlay .form-floating>label {
	position: absolute;
}
.image-wrapper.card:before {
	border-radius: 0.4rem !important;
}
@media (max-width: 991.98px) {
	.image-wrapper.bg-full {
		background-size: cover !important;
	}
}
@media (max-width: 767px) {
	.bg-map {
		background: none !important;
	}
}
@media (max-width: 575.98px) {
	.bg-xs-none {
		background-image: none !important;
	}
}
@media (max-width: 991.98px) {
	.image-wrapper.bg-cover {
		min-height: 25rem;
	}
}
@media (min-width: 992px) {
	.map.rounded-lg-start iframe,
	.image-wrapper.rounded-lg-start {
		border-radius: 0.1rem;
	}
	.image-wrapper.rounded-4-lg-start {
		border-radius: 0.8rem 0 0 0.8rem;
	}
}
@media (max-width: 991.98px) {
	.map.rounded-top iframe,
	.image-wrapper.rounded-top {
		border-top-left-radius: 0.4rem !important;
		border-top-right-radius: 0.4rem !important;
	}
	.image-wrapper.rounded-4-top {
		border-top-left-radius: 0.8rem !important;
		border-top-right-radius: 0.8rem !important;
	}
}
.filter {
	position: absolute;
	top:0px;
	left:0px;
	width: 100%;
	height: 100%;
	&.filter-black {
		background-color: rgba(0,0,0,0.8);
	}
	&.filter-white {
		background-color: rgba(255,255,255,0.8);
	}
	&.filter-primary {
		background-color: rgba(get-color(primary),0.8);
	}
	&.filter-secondary {
		background-color: rgba(get-color(secondary),0.8);
	}
	&.filter-warning {
		background-color: rgba(get-color(warning),0.8);
	}
	&.filter-alert {
		background-color: rgba(get-color(alert),0.8);
	}
}
.radius {
  @include border-radius;
}
.shadow {
  @include box-shadow;
